AAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;;EAEzB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;AACA,WAAW;AACX,qCAAqC;AACrC;EACE,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,WAAW;EACX,eAAe;EACf,aAAa;EACb,kBAAkB;EAClB,0LAA0L;EAC1L,4BAA4B;EAC5B,oCAAoC;EACpC,4CAA4C;AAC9C;AACA;;GAEG,WAAW;GACX,cAAc;GACd,kBAAkB;GAClB,mBAAmB;GACnB,cAAc;GACd,wBAAwB;AAC3B;AACA;GACG,aAAa;GACb,wBAAwB;AAC3B;AACA;EACE,MAAM,wBAAwB;AAChC;;AAEA,mBAAmB;AACnB;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,wBAAwB;EACxB,WAAW;EACX,aAAa;EACb,kBAAkB;EAClB,eAAe;EACf,iBAAiB;EACjB,gBAAgB;AAClB;AACA;EACE,qBAAqB;EACrB;AACF","sourcesContent":["* {\r\n    padding: 0;\r\n    margin: 0;\r\n    box-sizing: border-box;\r\n    font-family: 'Quicksand', sans-serif;\r\n}\r\n\r\nbody {\r\n    font-family: 'Quicksand', sans-serif;\r\n    background-color: white;\r\n    min-height: 100vh;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\np{\r\n  text-align: justify\r\n}\r\n\r\n/*\r\n    AppBar\r\n  */\r\n\r\n.app-bar {\r\n    padding: 8px 16px;\r\n    background-color: white;\r\n    display: grid;\r\n    grid-template-columns: auto 1fr auto;\r\n    gap: 10px;\r\n    position: sticky;\r\n    top: 0;\r\n    z-index: 99;\r\n    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.app-bar .app-bar__menu {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__menu button {\r\n    background-color: transparent;\r\n    border: none;\r\n    font-size: 18px;\r\n    padding: 8px;\r\n    cursor: pointer;\r\n}\r\n\r\n.app-bar .app-bar__brand {\r\n    display: flex;\r\n    align-items: center;\r\n}\r\n\r\n.app-bar .app-bar__brand h1 {\r\n    color: #19376D;\r\n    text-transform: uppercase;\r\n    font-size: 22px;\r\n    user-select: none;\r\n}\r\n\r\n.app-bar .app-bar__navigation {\r\n    position: absolute;\r\n    top: 50px;\r\n    left: -180px;\r\n    width: 150px;\r\n    transition: 0.5s  ease-in-out;\r\n    padding: 8px;\r\n    background-color: white;\r\n    overflow: hidden;\r\n    -webkit-transition: 0.5s  ease-in-out;\r\n    -moz-transition: 0.5s  ease-in-out;\r\n    -ms-transition: 0.5s  ease-in-out;\r\n    -o-transition: 0.5s  ease-in-out;\r\n}\r\n\r\n.app-bar .app-bar__navigation.open {\r\n    left: 0;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a {\r\n    display: inline-block;\r\n    text-decoration: none;\r\n    color: black;\r\n    padding: 8px;\r\n    margin-bottom: 5px;\r\n    width: 100%;\r\n}\r\n\r\n.app-bar .app-bar__navigation ul li a:hover {\r\n    text-decoration: none;\r\n    background-color:#19376D;\r\n    color: white;\r\n    border-radius: 5px;\r\n    font-weight: 900;\r\n    font-size: 1.1rem;\r\n    -webkit-border-radius: 5px;\r\n    -moz-border-radius: 5px;\r\n    -ms-border-radius: 5px;\r\n    -o-border-radius: 5px;\r\n    transition: 0.3s ease-in-out;\r\n    -webkit-transition: 0.3s ease-in-out;\r\n    -moz-transition: 0.3s ease-in-out;\r\n    -ms-transition: 0.3s ease-in-out;\r\n    -o-transition: 0.3s ease-in-out;\r\n}\r\n/*\r\n    Main Content\r\n  */\r\n\r\nmain {\r\n    padding: 32px;\r\n    flex: 1;\r\n}\r\n\r\n.content {\r\n    margin: 0 auto;\r\n    min-height: 100%;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content:space-between;\r\n    align-items: center;\r\n}\r\n.content input{\r\n  width: 40%;\r\n  padding: 0.7rem;\r\n  border-radius: 15px ;\r\n  border: 0.1rem solid #19376D;\r\n  -webkit-border-radius: 15px ;\r\n  -moz-border-radius: 15px ;\r\n  -ms-border-radius: 15px ;\r\n  -o-border-radius: 15px ;\r\n}\r\n\r\n.content .content__heading {\r\n    font-weight: normal;\r\n}\r\n#restaurant-list {\r\n    display: grid;\r\n    grid-template-columns: repeat(3, 1fr); /* 3 kolom dengan lebar yang sama */\r\n    gap: 20px; /* Spasi antara item */\r\n    margin-top: 20px;\r\n  }\r\n  \r\n  .restaurant-item {\r\n    background-color: #19376D;\r\n    border-radius: 10px;\r\n    overflow: hidden;\r\n    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n    -webkit-border-radius: 10px;\r\n    -moz-border-radius: 10px;\r\n    -ms-border-radius: 10px;\r\n    -o-border-radius: 10px;\r\n}\r\np.restaurant-item_P{\r\n  font-size: 1.2rem;\r\n  font-weight: bolder;\r\n}\r\n.restaurant-item a {\r\n    text-decoration: none;\r\n    color: white;\r\n  }\r\n  \r\n  .restaurant-item__thumbnail {\r\n    width: 100%;\r\n    height: 200px;\r\n    object-fit: cover;\r\n  }\r\n  \r\n  .restaurant-item__content {\r\n    padding: 16px;\r\n  }\r\n  \r\n  .restaurant-item__content h3 {\r\n    font-size: 20px;\r\n    margin-bottom: 8px;\r\n    color: white;\r\n  }\r\n  \r\n  .restaurant-item__content p {\r\n    margin-bottom: 4px;\r\n    color: #fff;\r\n  }\r\n  \r\n  .restaurant-item__content p strong {\r\n    color: #F3C623;\r\n  }\r\n  \r\n  /* Details */\r\n  #restaurant-detail h3{\r\n    margin-top: 30px;\r\n    margin-bottom: 10px;\r\n  }\r\n.restaurant-detail {\r\n  display: flex;\r\n  flex-direction: row;\r\n  margin-top: 20px;\r\n  padding: 20px;\r\n  background-color: #f5f5f5;\r\n  border-radius: 10px;\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n/* Header section */\r\n.restaurant-detail__header {\r\n  width: 70%; /* Perbaikan: Kurangi width dari 400% menjadi 40% agar proporsional */\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  margin-right: 20px; /* Gunakan margin-right daripada margin-bottom karena gambar dan konten ada di samping */\r\n}\r\n\r\n.restaurant-detail__header__image {\r\n  width: 100%;\r\n  height: 100%;\r\n  object-fit:fill;\r\n  border-radius: 10px;\r\n  -webkit-border-radius: 10px;\r\n  -moz-border-radius: 10px;\r\n  -ms-border-radius: 10px;\r\n  -o-border-radius: 10px;\r\n}\r\n\r\n.restaurant-detail__header__image img {\r\n  width: 100%;\r\n  height: auto;\r\n  object-fit: cover;\r\n  border-radius: 10px;\r\n}\r\n\r\n/* Rating */\r\n.restaurant-detail__header__rating {\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n  color: #333;\r\n  margin-top: 10px; /* Tambah sedikit jarak antara gambar dan rating */\r\n}\r\n.restaurant-detail__header__rating strong{\r\n  color: #F3C623;\r\n}\r\n/* Content section */\r\n.restaurant-detail__content {\r\n  flex: 1; /* Agar bagian konten memenuhi sisa ruang yang ada */\r\n  padding: 20px;\r\n  background-color: #fff;\r\n  border-radius: 10px;\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Tambahkan shadow agar terlihat menarik */\r\n}\r\n.restaurant-detail__content h3{\r\n  font-size: 1.5rem;\r\n  margin-left: 0.5rem;\r\n}\r\n.restaurant-detail__content p {\r\n  background-color: #f5f5f5;\r\n  padding: 10px;\r\n  border-radius: 0.5rem;\r\n  -webkit-border-radius: 0.5rem;\r\n  -moz-border-radius: 0.5rem;\r\n  -ms-border-radius: 0.5rem;\r\n  -o-border-radius: 0.5rem;\r\n}\r\n.restaurant-detail__content__title {\r\n  font-size: 1.5rem;\r\n  font-weight: bold;\r\n  margin-bottom: 10px;\r\n  margin-left: 0.5rem;\r\n}\r\n\r\n.restaurant-detail__content__menu {\r\n  margin-bottom: 20px;\r\n}\r\n\r\n.restaurant-detail__content__menu ul {\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr);\r\n  gap: 10px;\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 0;\r\n}\r\n\r\n.restaurant-detail__content__menu li {\r\n  margin-bottom: 10px;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n  font-size: 16px; /* Tambahkan font-size untuk konsistensi */\r\n}\r\n.restaurant-detail__content__menu ul li:nth-child(odd) {\r\n  background-color: #f5f5f5;\r\n}\r\n.restaurant-detail__content__menu ul li:nth-child(even) {\r\n  background-color: #e0e0e0;\r\n}\r\n/* Review section */\r\n.restaurant-detail__content__review {\r\n  margin-bottom: 20px;\r\n}\r\n.restaurant-detail__content__review__item:nth-child(odd) {\r\n  background-color: #fdfdfd;\r\n  padding: 10px;\r\n  border-radius: 5px;\r\n}\r\n\r\n.restaurant-detail__content__review__item:nth-child(even) {\r\n  background-color: #e0e0e0;\r\n  padding: 1rem;\r\n  border-radius: 0.5rem;\r\n  -webkit-border-radius: 0.5rem;\r\n  -moz-border-radius: 0.5rem;\r\n  -ms-border-radius: 0.5rem;\r\n  -o-border-radius: 0.5rem;\r\n}\r\n.restaurant-detail__content__review__item {\r\n  margin-bottom: 20px;\r\n  padding-bottom: 20px;\r\n  border-bottom: 1px solid #ddd;\r\n}\r\n\r\n.restaurant-detail__content__review__item h4 {\r\n  font-size: 18px;\r\n  font-weight: bold;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.restaurant-detail__content__review__item p {\r\n  font-size: 16px;\r\n  color: #666;\r\n  line-height: 1.5; /* Perbaikan: Tambah line-height untuk meningkatkan keterbacaan */\r\n}\r\n\r\n/* add-review*/\r\n.add-review {\r\n  margin-top: 20px;\r\n  padding: 20px;\r\n  background-color: #f5f5f5;\r\n  border-radius: 10px;\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.add-review h3 {\r\n  font-size: 24px;\r\n  font-weight: bold;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.add-review p {\r\n  font-size: 16px;\r\n  color: #666;\r\n  line-height: 1.5;\r\n}\r\n\r\n.add-review form {\r\n  margin-top: 20px;\r\n}\r\n\r\n.add-review form label {\r\n  display: block;\r\n  font-size: 16px;\r\n  font-weight: bold;\r\n  margin-bottom: 5px;\r\n}\r\n\r\n.add-review form input,\r\n.add-review form textarea {\r\n  width: 100%;\r\n  padding: 10px;\r\n  margin-bottom: 10px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 5px;\r\n}\r\n\r\n.add-review form button {\r\n  padding: 10px 20px;\r\n  background-color:#19376D;\r\n  color: #fff;\r\n  border: none;\r\n  border-radius: 5px;\r\n  cursor: pointer;\r\n  font-size: 16px;\r\n  font-weight: bold;\r\n}\r\n\r\n.add-review form button:hover {\r\n  background-color: #2c63c7dd;\r\n}\r\n/* restaurant likes */\r\n#restaurant-likes {\r\n  display: grid;\r\n  grid-template-columns: repeat(3, 1fr); /* 3 kolom dengan lebar yang sama */\r\n  gap: 20px; /* Spasi antara item */\r\n  margin-top: 20px;\r\n\r\n}\r\n.restaurant-likes {\r\n  background-color: #19376D;\r\n  border-radius: 10px;\r\n  overflow: hidden;\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);\r\n  -webkit-border-radius: 10px;\